@import url(https://fonts.googleapis.com/css?family=Lobster|Lora:400,400i,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
#main{
  display: flex;
  flex-direction: column;
  height: 100vh; /*chieu cao chiem toan bo khung nhin*/
  text-align: center;
  background-color: var(--main-bg-color);
  font-family: 'Lora', serif;
  font-size: 18px;
  color:var(--main-txt-color);
}
#title {
  padding: 20px;
  color: #fff;
  font-family: 'Lobster', cursive;
  font-weight: 400;
}
.container {
  display: grid;
  grid-template-rows: 50% 25% 25%;
  grid-template-columns: auto;
  margin: 0 auto;
  color:var(--main-txt-color);
  background-color: #fff;
  width: 80%;
  height: 70vh;
  border-radius: 7px;
}
.text-element{
  text-align: center;
  padding: 2% 2% 0px 2%;
  font-style: italic;
  height: 30vh;
  font-size: 3vw;
}

.author-element{
  text-align: right;
  padding: 2% 5%;
  font-size: 2vw;
  
}

.buttons-element{
  
  display: grid;
  grid-template-columns: 1fr 1fr 50%;
  grid-template-rows: auto;
}


#fb-button, #tweet-button, #new-quote{
  color: #fff;
  font-size: 2vh;
  border: none;
  border-radius: 7px;
  background-color: var(--main-bg-color);
  padding: 1vw 3vw 1vw 3vw;
  margin-top: 5vh;
}


#fb-button:hover, #tweet-button:hover, #new-quote:hover{
  background-color: #fff;
  color: var(--main-txt-color);
  border: 1px solid var(--main-txt-color);
}

@media screen and (max-width: 450px) {
  .container {
    display: grid;
    grid-template-rows: 50% 25% 25%;
    grid-template-columns: auto;
  }
  .text-element{
    
    font-size: 3vh;
  }
  .author-element{
   
    font-size: 2vh;
  }
}
